


























import { Component, Vue } from 'vue-property-decorator';
import { Action, Getter, State } from 'vuex-class';
import banner from '@/components/Banner.vue';
import { IQuestionItem, ITestTemplate } from '@/types/interface';
import questionInfoPanel from '../../components/QuestionInfoPanel.vue';
import questionResultPanel from '../../components/QuestionResultPanel.vue';

@Component({
  components: {
    banner,
    questionInfoPanel,
    questionResultPanel,
  },
})

export default class SolutionPage extends Vue {
  @Getter('testInfo', { namespace: 'onlineTest' }) testInfo!: ITestTemplate;
  @State('recordList', { namespace: 'onlineTest' }) stateRecordList!: IQuestionItem[];
  @Action('saveErrorQuestion', { namespace: 'pastTest' }) saveErrorQuestion!: (quz: IQuestionItem) => void

  send(res: IQuestionItem) {
    this.saveErrorQuestion(res);
    this.$router.push('/test/error_report');
  }

  testOther() {
    this.$router.push('/test/set_condition');
  }
}
